.page__body {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: @grey;
  font-family: Blogger Sans, Arial, sans-serif;
  color: @basic-blue-light;
  font-size: 16px;
  font-weight: normal;
  line-height: 18px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  width: auto;
  margin: 0 25px;
  @media (min-width:@tablet-size) {
    margin: 0 40px;
  }
  @media (min-width:@desktop-size) {
    width: 1321px;
    margin: 0 auto;
  }
}

.main-content__wrapper {
  display: flex;
  flex-direction: column;
  gap: 24px;
  @media (min-width:@tablet-size) {
    gap: 40px;
  }
  @media (min-width:@desktop-size) {
    flex-direction: row-reverse;
    gap: 86px;
  }
}

.font-style(@font-size; @line-height; @font-weight) {
  font-size: @font-size;
  line-height: @line-height;
  font-weight: @font-weight;
}
