  opacity: 0.8;
  transition: 0.2s ease-in-out;
}
.filter-form__item-caption:active {
  opacity: 0.6;
  transition: 0.2s ease-in-out;
}
.filter-form__item-checkbox + .filter-form__item-caption::before {
  content: "";
  position: absolute;
  left: 0;
  top: 1px;
  width: 18px;
  height: 18px;
  background-color: #ffffff;
  border-radius: 4px;
  z-index: 1;
}
