    width: 24px;
    height: 24px;
  }
}
.filter-form__item-checkbox:checked + .filter-form__item-caption::after {
  content: "";
  position: absolute;
  left: 5px;
  top: 5px;
  width: 8px;
  height: 9px;
  background-image: url(../img/icon-check.svg);
  background-repeat: no-repeat;
  border-radius: 4px;
  z-index: 2;
}
@media (min-width: 768px) {
  .filter-form__item-checkbox:checked + .filter-form__item-caption::after {
    left: 8px;
    top: 8px;
    width: 11px;
    height: 12px;
  }
}
