@font-face {
  font-family: "Blogger Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/BloggerSans-Regular.woff2") format("woff2"),
    url("../fonts/BloggerSans-Regular.woff") format("woff");
}

@font-face {
  font-family: "Blogger Sans";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/BloggerSans-Medium.woff2") format("woff2"),
    url("../fonts/BloggerSans-Medium.woff") format("woff");
}

@font-face {
  font-family: "Blogger Sans";
  font-weight: bold;
  font-style: bold;
  font-display: swap;
  src:
    url("../fonts/BloggerSans-Bold.woff2") format("woff2"),
    url("../fonts/BloggerSans-Bold.woff") format("woff");
}

