.form-container {
  padding: 26px 20px;
  background-color: @basic-yellow;
  border-radius: 20px;
  @media (min-width:@tablet-size) {
    padding: 37px 35px;
  }
  @media (min-width:@desktop-size) {
    width: 286px;
    box-sizing: border-box;
    padding: 44px 48px;
    height: min-content;
  }
}

.main-content__title {
  font-size: 22px;
  line-height: 100%;
  font-weight: 700;
  margin: 0;
  margin-bottom: 23px;
  width: min-content;
  @media (min-width:@tablet-size) {
    width: auto;
    font-size: 30px;
  }
}

.filter-form {
  @media (min-width:@tablet-size) {
    border-top: 1px solid @yellow;
    padding-top: 16px;
  }
  @media (min-width:@desktop-size) {
    border: none;
    padding: 0;
  }
}

.filter-form__block {
  padding: 13px 0;
  margin: 0;
  border: none;
  border-top: 1px solid @yellow;
  @media (min-width:@tablet-size) {
    padding: 16px 0;
    border: none;
  }
  @media (min-width:@desktop-size) {
    padding: 10px 0;
  }
}

.filter-form__block-container {
  @media (min-width:@tablet-size) {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
  }
  @media (min-width:@desktop-size) {
    flex-direction: column;
    align-items: start;
  }
}

.filter-form__title {
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 0;
  @media (min-width:@tablet-size) {
    min-width: 110px;
    font-size: 20px;
    line-height: 20px;
  }
}

.filter-form__list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 16px;
  margin-bottom: 7px;
  @media (min-width:@tablet-size) {
    margin: 0;
  }
}

.filter-form__list--standart {
  display: flex;
  flex-direction: column;
  gap: 12px;
  @media (min-width:@tablet-size) {
    flex-direction: row;
    gap: 18px;
  }
  @media (min-width:@desktop-size) {
    display: flex;
  flex-direction: column;
  }

  .filter-form__item {
    @media (min-width:@tablet-size) {
      min-width: 150px;
    }
    @media (min-width:@tablet-size) {
      min-width: none;
    }
  }
}

.filter-form__list--special {
  display: flex;
  flex-direction: row;
  gap: 2px;
}

.filter-form__item-caption {
  position: relative;
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
  padding-left: 27px;
  cursor: pointer;
  @media (min-width:@tablet-size) {
    flex-direction: row;
    gap: 18px;
    font-size: 20px;
    padding-left: 36px;
  }

  &:hover, &:focus {
    opacity: 0.8;
    transition: 0.2s ease-in-out;
  }
  &:active {
    opacity: 0.6;
    transition: 0.2s ease-in-out;
  }
}

.filter-form__item-checkbox + .filter-form__item-caption::before {
  content: "";
  position: absolute;
  left: 0;
  top: 1px;
  width: 18px;
  height: 18px;
  background-color: @white;
  border-radius: 4px;
  z-index: 1;
  @media (min-width:@tablet-size) {
    width: 24px;
    height: 24px;
  }
}

.filter-form__item-checkbox:checked + .filter-form__item-caption::after {
  content: "";
  position: absolute;
  left: 5px;
  top: 5px;
  width: 8px;
  height: 9px;
  background-image: url(../img/icon-check.svg);
  background-repeat: no-repeat;
  border-radius: 4px;
  z-index: 2;
  @media (min-width:@tablet-size) {
    left: 8px;
    top: 8px;
    width: 11px;
    height: 12px;
  }
}

.filter-form__item-caption-special {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: @white;
  position: relative;
  display: block;
  cursor: pointer;
  @media (min-width:@tablet-size) {
    width: 44px;
    height: 44px;
  }

  &:hover, &:focus {
    opacity: 0.8;
    transition: 0.2s ease-in-out;
  }
  &:active {
    opacity: 0.6;
    transition: 0.2s ease-in-out;
  }
}

.filter-form__item-img {
  position: absolute;
  content: "";
  top: 8px;
  left: 8px;
  opacity: 0.15;
  @media (min-width:@tablet-size) {
    width: 23px;
    height: 22px;
    top: 11px;
    left: 10px;
  }
}

.filter-form__item-checkbox:checked + .filter-form__item-caption-special .filter-form__item-img {
  opacity: 1;
}

.filter-form__range-controls {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 25px;
  margin-top: 16px;
  margin-bottom: 7px;
  @media (min-width:@tablet-size) {
    flex-direction: row-reverse;
    align-items: center;
    gap: 60px;
    justify-content: space-between;
  }
  @media (min-width:@desktop-size) {
    flex-direction: column;
    gap: 36px;
  }
}

.filter-form__level-controls {
  background-color: @white;
  border: 1px solid @light-grey;
  border-radius: 4px;
  display: flex;
  flex-direction: row;
  position: relative;
  @media (min-width:@tablet-size) {
    min-width: 148px;
    box-sizing: border-box;
  }
  @media (min-width:@desktop-size) {
    min-width: auto;
  }
}

.filter-form__level-controls::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  height: 1px;
  width: 16px;
  background-color: @light-grey;
  transform: translate(-50%, 0);
}

.filter-form__level-input {
  font-family: Blogger Sans, Arial, sans-serif;
  width: 50%;
  font-size: 14px;
  line-height: 13px;
  font-weight: 500;
  text-align: center;
  border: none;
  padding-top: 13px;
  padding-bottom: 8px;
  position: relative;
  @media (min-width:@tablet-size) {
    padding-top: 15px;
    padding-bottom: 11px;
    font-size: 20px;
    line-height: 20px;
  }
}

.filter-form__scale {
  position: relative;
  width: 100%;
  height: 2px;
  background-color: fade(@basic-blue-light, 20%);
  @media (min-width:@tablet-size) {
    min-width: 280px;
  }
  @media (min-width:@desktop-size) {
    min-width: auto;
    width: 100%;
  }
}

.filter-form__bar {
  height: inherit;
  background-color: @basic-blue-light;
}

.filter-form__toggle {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: @basic-blue-light;
  bottom: -4px;
  cursor: pointer;
  @media (min-width:@tablet-size) {
    width: 13px;
    height: 13px;
    bottom: -6px;
  }

  &:hover, &:focus {
    opacity: 0.8;
    transition: 0.2s ease-in-out;
  }
  &:active {
    opacity: 0.6;
    transition: 0.2s ease-in-out;
  }
}

.filter-form__submit-wrapper {
  margin: 5px 0;
  display: flex;
  justify-content: center;
  @media (min-width:@tablet-size) {
    border-top: 1px solid @yellow;
    padding-top: 36px;
  }
  @media (min-width:@desktop-size) {
    border: none;
    padding: 0;
    margin: 0;
    margin-top: 45px;
  }
}

.filter-form__submit-button {
  color: @basic-blue;
  font-size: 17px;
  line-height: 100%;
  font-weight: 700;
  text-transform: uppercase;
  padding: 14px 20px;
  min-height: 40px;
  text-align: center;
  border: none;
  border-radius: 25px;
  background-color: @white;
  width: 100%;
  font-family: Blogger Sans, Arial, sans-serif;
  cursor: pointer;
  @media (min-width:@tablet-size) {
    font-size: 20px;
  }
  @media (min-width:@desktop-size) {
    min-height: 49px;
    padding: 17px 20px;
  }

  &:hover, &:focus {
    color: fade(@basic-blue, 70%);
    transition: 0.2s ease-in-out;
  }
  &:active {
    color: fade(@basic-blue, 50%);
    transition: 0.2s ease-in-out;
  }
}
